            animation: expand .5s ease-in-out;
}

/* LOGIN */

.container-login{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40vw;
}
.container-login form {
    /* width: 250px; */
    -webkit-animation: expand .5s ease-in-out;
            animation: expand .5s ease-in-out;
    border: 1px solid grey;
    border-radius: 10px;
    padding: 2rem;
}
@-webkit-keyframes expand{
    from {
        -webkit-transform: scaleX(0);
                transform: scaleX(0);
    }
    to {
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
    }
}
@keyframes expand{
    from {
        -webkit-transform: scaleX(0);
                transform: scaleX(0);
    }
    to {
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
    }
}
.container-login form img {
    width: 180px;
    margin-bottom: 2.5rem;
    padding: 1rem;
}
